@import url(https://fonts.googleapis.com/css2?family=Alumni+Sans:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Alumni+Sans:wght@300&display=swap);
:root {
    --nav-color: rgb(255, 255, 255);
    --nav-font-size: calc(3px + 2vmin);
    --logo-font-size: calc(6px + 2vmin);
    --nav-box-shadow: 0px 0px 9px 3px rgba(41, 41, 41, 0.25);
    --navbar-height: 5vh;
    --m-font: 'Alumni Sans';
}

/* SELECTORS */
/* Direct */
* {
    margin: 0;
    padding: 0;
}


.navbar {
    top: 1;
    left: 0;
    /* position: fixed; */
    min-height: 5vh;
    min-height: var(--navbar-height);
    justify-content: space-around;
    width: 100%;
    background-color: rgb(255, 255, 255);
    background-color: var(--nav-color);
    display: flex;
    flex-direction: row;
    align-items: center;
    box-shadow: 0px 0px 9px 3px rgba(41, 41, 41, 0.25);
    box-shadow: var(--nav-box-shadow);
    z-index: 1000;
}

nav ul {
    display: flex;
    list-style-type: none;
    justify-content: space-around;
    flex-direction: row;
    width: 30%;
}

nav ul li {
    font-family: 'Alumni Sans';
    font-family: var(--m-font);
    font-size: calc(3px + 2vmin);
    font-size: var(--nav-font-size);
}

nav ul li a {
    color: black;
    text-decoration: none;
}

.left {
    float: left;
}

.logo-text {
    font-weight: bold;
    font-family: 'Alumni Sans';
    font-family: var(--m-font);
    font-size: calc(6px + 2vmin);
    font-size: var(--logo-font-size);
    padding-left: 5px;
    color: black;
    text-decoration: none;
}

.collapse {
    display: none;
    cursor: pointer;
}

.collapse div {
    width: 25px;
    height: 2px;
    background-color: black;
    margin: 5px;
}

.banner {
    top: 0;
    color: rgb(245, 245, 245);
    background-color:rgb(0, 0, 0);
    position: fixed;
    width: 100%;
    justify-content: center;
    text-align: center;
    align-items: center;
    font-family: 'Alumni Sans';
    font-family: var(--m-font);
}

@media screen and (max-width: 1180px) {
    nav ul {
        width: 50%;
    }
}

@media screen and (max-width: 1024px) {
    body {
        overflow-x: hidden;
    }

    nav ul {
        position: absolute;
        right: 0px;
        height: calc( 100vh - 5vh);
        height: calc( 100vh - var(--navbar-height) );
        top: 8vh;
        background-color: rgb(255, 255, 255);
        background-color: var(--nav-color);
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        width: 50%;
        -webkit-transform: translateX(100%);
                transform: translateX(100%);
        transition: -webkit-transform 0.3s ease-in;
        transition: transform 0.3s ease-in;
        transition: transform 0.3s ease-in, -webkit-transform 0.3s ease-in;
    }

    nav ul li {
        opacity: 0;
    }

    .collapse {
        display: block;
    }
}

.nav-active {
    -webkit-transform: translateX(0%);
            transform: translateX(0%);
}

@-webkit-keyframes navLinkFade {
    from {
        opacity: 0;
        -webkit-transform: translateX(50px);
                transform: translateX(50px);
    }

    to {
        opacity: 1;
        -webkit-transform: translateX(0px);
                transform: translateX(0px);
    }
}

@keyframes navLinkFade {
    from {
        opacity: 0;
        -webkit-transform: translateX(50px);
                transform: translateX(50px);
    }

    to {
        opacity: 1;
        -webkit-transform: translateX(0px);
                transform: translateX(0px);
    }
}



